import React, { useState, useEffect, useRef } from 'react'
import { Form } from 'antd';

import {
    BoxLottery,
    InputLable,
    ButtonLottery,
} from "components";
import { formatNumberDecime } from 'helper/helper'
import './style.scss'
import { connect } from 'react-redux';


const FormWithdraw = ({
    bankid,
    banknumber,
    username,
    getUserInfo,
    onRequestWithdraw,
    banksInfo,
    onUpdateData,
    resetForm,
    onGetAllBanks
}) => {
    const formRef = useRef(null)
    const [loading, SetLoading] = useState(false)
    const [infoBank, SetInfoBank] = useState()



    useEffect(() => {
        getUserInfo()
        onGetAllBanks()

    }, [])
    useEffect(() => {
        const bank = banksInfo.find(item => item.id == Number(bankid));

        if (bank?.id) {
            const bankname = bank?.bankname || ''
            SetInfoBank(`${bankname} ${banknumber} ${username}`)
        }
    }, [bankid])
    useEffect(() => {

        //reset form
        if (resetForm) {


            onUpdateData({ resetForm: false })
            formRef.current.resetFields()
        }
    }, [resetForm])




    const onFinish = async (value = {}) => {
        SetLoading(true)

        await onRequestWithdraw({ ...value }).then().catch()
        SetLoading(false)
    }
    return (
        <div className="form-deposit">
            <BoxLottery
                style={{ width: '100%', display: 'flex', height: '100%' }}
                span={24}
            >
                <div className="content pb-2">
                    <Form
                        ref={formRef}
                        name="basic"
                        layout="vertical"
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={onFinish}
                    >
                        <Form.Item
                            name="amountmoneywithdraw"
                            className="mb-075"
                            rules={[{
                                required: true,
                                message: ' '
                            }]}
                        >
                            <div>
                                <InputLable
                                    lable=" "
                                    input={{
                                        placeholder: "",
                                        readOnly: loading,
                                        name: "amount",
                                        onKeyUp: () => formatNumberDecime('input_deposit'),
                                        onKeyDown: () => formatNumberDecime('input_deposit'),
                                        onKeyPress: () => formatNumberDecime('input_deposit'),
                                        onChange: () => formatNumberDecime('input_deposit'),
                                        id: "input_deposit",

                                    }}
                                />
                            </div>
                        </Form.Item>
                        <Form.Item
                            name="withdrawpassword"
                            className="mb-075"
                            rules={[{
                                required: true,
                                message: ' '
                            }]}
                        >
                            <div>
                                <InputLable
                                    lable=" "
                                    input={{
                                        type: 'password',
                                        placeholder: "",
                                        maxLength: 4
                                    }}
                                />
                            </div>
                        </Form.Item>
                        <Form.Item className="mb-075" >
                            <InputLable
                                lable=" "
                                input={{
                                    placeholder: "",
                                    readOnly: true,
                                    value: infoBank
                                }}
                            />
                        </Form.Item>


                        {/* //button */}
                        <Form.Item className="mb-0 mt-1">
                            <div className="flex-center bnt-content-policy">
                                <ButtonLottery
                                    text=""
                                    button={{
                                        htmlType: "submit",
                                        loading: loading,
                                    }}
                                />
                            </div>

                        </Form.Item>
                    </Form>

                </div>
            </BoxLottery>
        </div>
    )
}

export default connect(
    state => {
        const { auth, common, history } = state

        return {
            bankid: auth?.auth?.bankid || '0',
            banknumber: auth?.auth?.banknumber || '',
            username: auth?.auth?.username || '',
            banksInfo: common?.banksInfo?.rows || [],
            resetForm: history?.resetForm,

        }
    },
    ({
        auth: { getUserInfo },
        history: { onRequestWithdraw, onUpdateData },
        common: { onGetAllBanks }

    }) => {
        return {
            getUserInfo,
            onRequestWithdraw,
            onUpdateData,
            onGetAllBanks
        }
    }
)(FormWithdraw)