import React, { useState } from 'react'
import PropTypes from 'prop-types';
import { Form, Input } from 'antd';
import { connect } from 'react-redux';

import {
    BoxLottery,
    InputLable,
    ButtonLottery,
    TitleBox,
    SelectedLottery
} from "components";
import { QUESTION_SELECT } from 'config/common'
import './style.scss'
const { TextArea } = Input;




const FormRequire = ({ onCreateQuestion }) => {
    const [loading, SetLoading] = useState(false)
    const [questioncategoryid, SetQuestioncategoryid] = useState()


    const onFinish = async (value = {}) => {
        SetLoading(true)

        await onCreateQuestion({
            ...value,
            questioncategoryid: questioncategoryid
        }).then().catch()
        SetLoading(false)
    }
    return (
        <div className="form-require">
            <div className="mb-5">
                <TitleBox name='' />
            </div>

            <BoxLottery
                style={{ width: '100%', display: 'flex', height: '100%' }}
                span={24}
            >
                <div className="content pb-2">
                    <Form
                        name="basic"
                        layout="vertical"
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={onFinish}
                    >
                        <div className="flex-center info_selected">
                            <Form.Item
                                name="questioncategoryid"
                                className="mb-075 w-50 mr-3"
                                rules={[{
                                    required: !questioncategoryid ? true : false,
                                    message: ' '
                                }]}
                            >
                                <SelectedLottery
                                    lable=""
                                    onUpdateSelected={value => SetQuestioncategoryid(value)}
                                    data={QUESTION_SELECT}
                                />
                            </Form.Item>
                            <Form.Item
                                name="title"
                                className="mb-075 w-50"
                                rules={[{
                                    required: true,
                                    message: ' '
                                }]}
                            >
                                <div className=" ml-3">
                                    <InputLable
                                        lable=""
                                        input={{
                                            placeholder: "",
                                            readOnly: loading,
                                        }}
                                    />
                                </div>

                            </Form.Item>

                        </div>
                        <Form.Item
                            name="fullcontent"
                            className="mb-075"
                            rules={[{
                                required: true,
                                message: ' '
                            }]}
                        >
                            <TextArea autoSize={{ minRows: 10, maxRows: 10 }} />
                        </Form.Item>


                        {/* //button */}
                        <Form.Item className="mb-0 mt-1">
                            <div className="flex-center bnt-content-require">
                                <div className="flex-center group-contatct-us">
                                    <a href="#" className="icon-contact-us tele flex-center">
                                        <img src={require(`resources/images/icon/ic_tele.png`)} />
                                    </a>
                                    <a href="#" className="ml-2 icon-contact-us talk flex-center">
                                        <img src={require(`resources/images/icon/ic_talk.png`)} />
                                    </a>
                                </div>
                                <ButtonLottery
                                    text=""
                                    button={{
                                        htmlType: "submit",
                                        loading: loading,
                                    }}
                                />
                            </div>

                        </Form.Item>
                    </Form>

                </div>
            </BoxLottery>
        </div>
    )
}


FormRequire.propTypes = {
    onCreateQuestion: PropTypes.func,
};
export default connect(
    null,
    ({ auth: { onCreateQuestion } }) => {
        return {
            onCreateQuestion
        }
    }
)(FormRequire)