import React from 'react'
import PropTypes from 'prop-types';
import { Row, Col } from 'antd';

import PolicyDeposit from './components/PolicyDeposit'
import FormDeposit from './components/FormDeposit'
import FormWithdraw from './components/FormWithdraw'
import {
    BoxLottery,
    TitleBox,
} from "components";
import './style.scss'


const DepositAndWithdraw = ({
    type
}) => {

    const title = type == 'WITHDRAW' ? '   ' : '   '
    const renderComponent = type == 'WITHDRAW' ? <FormWithdraw /> : <FormDeposit />
    return (
        <div className="pb-5 pt-3 m-deposit" >
            <BoxLottery
                style={{ width: '100%', display: 'flex' }}
                span={24}
            >
                <div className="content">
                    <Row>
                        <Col xxl={14} xl={14} lg={14} md={14} span={24}>
                            <TitleBox name={title} fontSize={'1rem'} hiddenGach />
                            <PolicyDeposit type={type} style={{ height: '100%', 'line-height': '100%' }} />
                        </Col>
                        <Col xxl={10} xl={10} lg={10} md={10} span={24}>
                            {renderComponent}
                        </Col>
                    </Row>

                </div>
            </BoxLottery>
        </div>
    )
}


DepositAndWithdraw.propTypes = {
    type: PropTypes.string,
};
export default DepositAndWithdraw
