import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { Row, Col } from "antd";
import { connect } from "react-redux";
import { convertTimeUTCtoLocal } from "helper/helper";
import moment from "moment";

import { BoxLottery, TitleBox } from "components";
import "./style.scss";

const CheckingSystem = ({ title, data, onGetDataCheckingTheOnSystem }) => {
  useEffect(() => {
    onGetDataCheckingTheOnSystem();
  }, []);

  const timeConvert = (date = new Date()) => {
    date = moment(convertTimeUTCtoLocal(date)).format("YYYY.MM.DD HH:mm");
    return date;
  };
  return (
    <div className="pb-5 pt-3 m-checking-the-system">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="content">
          <Row>
            <Col span={24}>
              <TitleBox name={data?.title || ""} fontSize={"1rem"} hiddenGach />
              <div className="content-checking-system flex-center">
                <div className="f-16">
                  <p>{data?.fullcontent || ""}</p>
                  <div className="pt-2">
                    <span> </span>
                    <br />
                    <span>
                      {` ${timeConvert(
                        data?.fromdate
                      )} ~  ${timeConvert(data?.todate)}`}
                    </span>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </div>
      </BoxLottery>
    </div>
  );
};

CheckingSystem.propTypes = {
  type: PropTypes.string,
  onGetDataCheckingTheOnSystem: PropTypes.func,
  data: PropTypes.object,
};
export default connect(
  (state) => {
    const { common = {} } = state;

    return {
      data: common?.systemCheckingData || {},
    };
  },
  ({ common: { onGetDataCheckingTheOnSystem } }) => {
    return { onGetDataCheckingTheOnSystem };
  }
)(CheckingSystem);
