import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Spin } from "antd";

import { BoxLottery, TitleBox, Countdown } from "components";
import { getColorFollowValue, renderTextDate } from "helper/game";
import "./style.scss";
import { isArray, isNumber } from "lodash";

const ResultGame = ({
  result,
  getResult,
  type,
  reLoadDataGame,
  typeGame,
  endRoundOfGame,
  roundofgame,
}) => {
  const [loading, SetLoading] = useState(false);
  useEffect(() => {
    getDataResult();
  }, []);

  const getDataResult = async () => {
    SetLoading(true);
    
    await getResult({
      typeGame: typeGame,
      type: type,
    });
    SetLoading(false);
  };

  const onReloadData = () => {
    reLoadDataGame({
      typeGame: typeGame,
      type: type,
    });
  };
  const renderListResult =
    isArray(result.data) &&
    result.data.map((item, index) => {
      if (isNumber(item)) {
        if (
          (index == 6 && type == "LOTTO") ||
          (index == 5 && type !== "LOTTO")
        ) {
          return (
            <div key={String(index)} className="flex-center">
              <span className="ic-plus mr-2 f-20">+</span>
              <div
                className="item flex-center"
                style={{ borderColor: getColorFollowValue(item) }}
              >
                <span>{item || 0}</span>
              </div>
            </div>
          );
        }

        return (
          index <= (type == "LOTTO" ? 5 : 4) && (
            <div key={String(index)}>
              <div
                className="item flex-center mr-2"
                style={{ borderColor: getColorFollowValue(item) }}
              >
                <span>{item || 0}</span>
              </div>
            </div>
          )
        );
      }
    });
  return (
    <div className="result-game">
      <BoxLottery
        style={{
          width: "100%",
          textAlign: "center",
        }}
        span={24}
      >
        <div className="content-result">
          <div className="title">
            <TitleBox
              name={`${
                type == "LOTTO" ? result?.drawno || 0 : result?.roundofdate || 0
              } `}
              hiddenGach
              fontSize={20}
            />
            <span className="font-small" style={{ marginTop: -10 }}>
              {renderTextDate(result?.createdat || new Date())}
            </span>
          </div>
          <div className="result flex-center mt-4 mb-4">{renderListResult}</div>
          <div className="count-down flex-center">
            <span className="mr-1">{`${roundofgame}   `}</span>
            {(endRoundOfGame && (
              <Countdown
                dateTime={endRoundOfGame}
                callback={onReloadData}
                type={type}
              />
            )) ||
              null}
          </div>
        </div>

        {loading && (
          <div className="loading flex-center spin">
            <Spin />
          </div>
        )}
      </BoxLottery>
    </div>
  );
};

ResultGame.propTypes = {
  result: PropTypes.object,
  getResult: PropTypes.func,
  type: PropTypes.string,
  reLoadDataGame: PropTypes.func,
  typeGame: PropTypes.number,
  roundofgame: PropTypes.number,
  endRoundOfGame: PropTypes.string,
};
export default ResultGame;
