import React, { useEffect, useState } from "react";
import "./style.scss";
import {
  BoxLottery,
  TitleBox,
  ButtonLottery,
  InputLable,
  SelectedLottery,
} from "components";
import PropTypes from "prop-types";
import { Form } from "antd";
import { connect } from "react-redux";

const RegisterForm = ({ onNextStep, banks, onRegister }) => {
  const [loading, SetLoading] = useState(false);
  const [bankId, SetBankId] = useState(false);

  const onFinish = async (value = {}) => {
    SetLoading(true);
    await onRegister({
      ...value,
      bankid: bankId,
    })
      .then((res) => {
        if (res) onNextStep(3);
      })
      .catch();

    SetLoading(false);
  };
  return (
    <div className="step-2">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="pt-3 pb-5 title-step-2">
          <TitleBox name=" " fontSize={"1rem"} hiddenGach />
        </div>

        <div className="content">
          <Form
            name="basic"
            layout="vertical"
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
          >
            <Form.Item
              name="username"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable lable="" input={{ placeholder: "" }} />
                <span className="mb-1 ml-3 txt">
                    ,   4~14 
                </span>
              </div>
            </Form.Item>
            <Form.Item
              name="password"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable
                  lable=""
                  input={{ placeholder: "", type: "password", maxLength: 16 }}
                />
                <span className="mb-1 ml-3 txt">
                  8~16 , ,  
                </span>
              </div>
            </Form.Item>
            <Form.Item
              name="ol_password"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable
                  lable=" "
                  input={{ placeholder: "", type: "password", maxLength: 16 }}
                />
                <span className="mb-1 ml-3 txt">
                      .
                </span>
              </div>
            </Form.Item>
            <Form.Item
              name="phonenumber"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable lable="" input={{ placeholder: "" }} />
                <span className="mb-1 ml-3 txt"> .</span>
              </div>
            </Form.Item>
            <Form.Item
              name="bankid"
              className="mb-075"
              rules={[
                {
                  required: bankId > 0 ? false : true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <SelectedLottery
                  lable=" "
                  data={banks}
                  onUpdateSelected={(code) => SetBankId(Number(code))}
                />
                <span className="mb-1 ml-3 txt"></span>
              </div>
            </Form.Item>
            <Form.Item
              name="banknumber"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable lable="" input={{ placeholder: "" }} />
                <span className="mb-1 ml-3 txt">-   .</span>
              </div>
            </Form.Item>
            <Form.Item
              name="bankholdername"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable lable="" input={{ placeholder: "" }} />
                <span className="mb-1 ml-3 txt">
                      .
                </span>
              </div>
            </Form.Item>
            <Form.Item
              name="withdrawpassword"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable
                  lable=" "
                  input={{ placeholder: "", type: "password" }}
                />
                <span className="mb-1 ml-3 txt">
                      4   
                </span>
              </div>
            </Form.Item>
            <Form.Item
              name="resellercode"
              className="mb-075"
              rules={[
                {
                  required: true,
                  message: "",
                },
              ]}
            >
              <div className="d-flex align-end pb-3 flex-wrap">
                <InputLable lable="" input={{ placeholder: "" }} />
                <span className="mb-1 ml-3 txt">
                      .
                </span>
              </div>
            </Form.Item>

            {/* //button */}
            <Form.Item className="mb-0 pt-3">
              <div className="flex-center justify-between mt-5 bnt-content-policy">
                <ButtonLottery
                  text=""
                  primary
                  button={{
                    onClick: () => onNextStep(1),
                  }}
                />
                <ButtonLottery
                  text=""
                  button={{
                    htmlType: "submit",
                    loading: loading,
                  }}
                />
              </div>
            </Form.Item>
          </Form>
        </div>
      </BoxLottery>
    </div>
  );
};

RegisterForm.propTypes = {
  onNextStep: PropTypes.func,
};

export default connect(
  (state) => {
    const { common } = state;

    return {
      banks: common?.banksInfo?.rows || [],
    };
  },
  ({ auth: { onRegister } }) => {
    return {
      onRegister,
    };
  }
)(RegisterForm);
