import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { Col, Row } from "antd";

import "./style.scss";
import { NUMBER_SELECT, NUMBER_SELECT_2 } from "config/togger";
import { ButtonLottery } from "components";

const BoxConfirmSelect = ({
  onSentResultOptions,
  onRandomNumberMinMax,
  type,
}) => {
  const [dataSelect, SetDataSelect] = useState([]);
  const [select_2, SetSelect2] = useState();
  const [select, SetSelect] = useState([]);
  const _titleType =
    type == "LOTTO"
      ? "6  "
      : "5    ";

  useEffect(() => {
    resetForm();
  }, []);

  const resetForm = () => {
    const arr = [...NUMBER_SELECT].map((_i) => {
      return {
        ..._i,
        selected: false,
      };
    });

    SetDataSelect(arr);
    SetSelect([]);
    SetSelect2(null);
  };

  const onSelectedItem = (item = {}) => {
    const data = [...dataSelect];
    const maxSelect = type == "LOTTO" ? select.length == 6 : select.length == 5;

    if (!data[item.value - 1].selected && maxSelect) return;
    data[item.value - 1].selected = !data[item.value - 1].selected;
    SetDataSelect(data);

    if (data[item.value - 1].selected) {
      SetSelect([...select, { ...item }]);
    } else {
      SetSelect(select.filter((i) => i?.value !== item?.value));
    }
  };

  const sentSelected = (isRamdom = false) => {
    let option = [...select];
    if (select_2 || select_2 === 0) option.push({ value: select_2 });

    onSentResultOptions({
      option,
      isRamdom,
    })
      .then((res) => {
        if (res) resetForm();
      })
      .catch();
  };

  const getClassName = (type) => {
    switch (type) {
      case "LOTTO": {
        return "lotto-game";
      }
      default:
        return "powerball-game";
    }
  };

  return (
    <div className="select-number mt-3 mr-1">
      <Row>
        <Col span={24}>
          <div className=" content">
            <div className="title">
              <p className="bold m-0 f-16"> </p>
              <p className="small m-0 f-12">{_titleType}</p>
            </div>

            <div className={`select-box d-flex ${getClassName(type)}`}>
              <div
                className="group-select-1"
                style={type !== "LOTTO" ? { width: "70%" } : {}}
              >
                <div className="d-flex flex-wrap">
                  {(dataSelect || []).map((item, index) => {
                    if (index < (type == "LOTTO" ? 45 : 28)) {
                      return (
                        <div
                          key={String(index)}
                          className="item"
                          onClick={() => onSelectedItem(item)}
                        >
                          <span
                            className={` flex-center ${
                              (item.selected && "active") || ""
                            }`}
                          >
                            {item?.value}
                          </span>
                        </div>
                      );
                    }
                  })}
                </div>
              </div>
              {type !== "LOTTO" && (
                <div
                  className="d-flex justify-between group-select-2 ml-1"
                  style={{ width: "30%" }}
                >
                  <div>
                    {NUMBER_SELECT_2.map((item, index) => {
                      if (index <= 6) {
                        return (
                          <div
                            key={String(index)}
                            className="item"
                            onClick={() => SetSelect2(item)}
                          >
                            <span
                              className={` flex-center ${
                                (select_2 == item && "active") || ""
                              }`}
                            >
                              {item}
                            </span>
                          </div>
                        );
                      }
                    })}
                  </div>
                  <div>
                    {NUMBER_SELECT_2.map((item, index) => {
                      if (index > 6) {
                        return (
                          <div
                            key={String(index)}
                            className="item"
                            onClick={() => SetSelect2(item)}
                          >
                            <span
                              className={` flex-center ${
                                (select_2 == item && "active") || ""
                              }`}
                            >
                              {item}
                            </span>
                          </div>
                        );
                      }
                    })}
                  </div>
                </div>
              )}
            </div>
            <div className="button-confirm flex-center">
              <ButtonLottery
                text=""
                primary
                button={{
                  style: { marginRight: "0.5rem" },
                  onClick: () => onRandomNumberMinMax(true),
                }}
              />
              <ButtonLottery
                text=""
                button={{
                  style: { marginLeft: "0.5rem" },
                  onClick: () => sentSelected(false),
                }}
              />
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};

BoxConfirmSelect.propTypes = {
  onSentResultOptions: PropTypes.func,
  onRandomNumberMinMax: PropTypes.func,
  type: PropTypes.string,
};

export default BoxConfirmSelect;
