import React from "react";
import "./style.scss";
import { BoxLottery, TitleBox, ButtonLottery } from "components";
import PropTypes from "prop-types";
import { STEP1_POLICY_REGISTER } from "config/togger";
import { Checkbox, Form } from "antd";

const Policy = ({ onNextStep }) => {
  const onFinish = async (values) => {
    onNextStep(2);
  };
  return (
    <div className="step-1">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="content">
          <Form
            name="basic"
            layout="vertical"
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
          >
            {STEP1_POLICY_REGISTER.map((item, index) => {
              return (
                <div
                  key={String(index)}
                  className={`content-policy_ ${index > 0 && "pt-3"}`}
                >
                  <TitleBox
                    name={item?.title || ""}
                    fontSize={"1rem"}
                    hiddenGach
                  />
                  <div
                    className="content-policy mb-3"
                    id="scroll"
                    dangerouslySetInnerHTML={{ __html: item?.content }}
                  ></div>
                  <Form.Item
                    name={`agreement${index}`}
                    valuePropName="checked"
                    rules={[
                      {
                        validator: (_, value) =>
                          value
                            ? Promise.resolve()
                            : Promise.reject(
                                new Error("   .")
                              ),
                      },
                    ]}
                  >
                    <div className="content-checkbox">
                      <Checkbox>
                        <span className="cbox-text">{item?.cbTxt || ""}</span>
                      </Checkbox>
                    </div>
                  </Form.Item>
                </div>
              );
            })}

            <Form.Item className="mb-0 pt-5 ">
              <div className="flex-center justify-between mt-5 bnt-content-policy">
                <ButtonLottery
                  text=""
                  bntHref
                  primary
                  button={{
                    href: "/login",
                  }}
                />
                <ButtonLottery
                  text=""
                  button={{
                    htmlType: "submit",
                  }}
                />
              </div>
            </Form.Item>
          </Form>
        </div>
      </BoxLottery>
    </div>
  );
};

Policy.propTypes = {
  onNextStep: PropTypes.func,
};
export default Policy;
