import React, { useState, useEffect } from "react";
import PropTypes, { number } from "prop-types";
import { Col, Row, Button } from "antd";
import { connect } from "react-redux";

import { ButtonLottery } from "components";
import "./style.scss";
import { getColorFollowValue } from "helper/game";
import { formatMoney } from "helper/helper";
import snackbar from "utils/snackbar-utils";

const BoxSelectNumber = ({
  orderItem,
  onRemoveItemSelectd,
  value,
  balance,
  onBetGameOrder,
  loading,
  type,
}) => {
  const [option, SetDataOption] = useState([]);
  const _value = value == 1000005 ? 10000 : value;

  useEffect(() => {
    const leg = orderItem.length;
    const data = [];

    for (let i = 0; i < 5; i++) {
      if (i < leg) data.push({ ...orderItem[i] });
      else data.push({ result: [-1, -1, -1, -1, -1, -1] });
    }

    SetDataOption([...data]);
  }, [orderItem]);

  const renderItems = option.map((item, index) => {
    return (
      <div
        className="item flex-center mb-3"
        key={String(index) + (item?.fullbet || "")}
      >
        <div className="flex-center justify-between item-li">
          <p className="m-0 f-16 _title">{item?.type || ""}</p>

          <div className="group-result d-flex">
            {item?.result.map((_i, _ind) => {
              return (
                <div
                  className="_i flex-center mr-1"
                  key={String(_ind)}
                  style={
                    Number(_i) > -1
                      ? { background: getColorFollowValue(_i) }
                      : {}
                  }
                >
                  <span className="f-16">{Number(_i) > -1 ? _i : ""}</span>
                </div>
              );
            })}
          </div>
        </div>

        <Button
          className="bnt-cancel"
          onClick={() => onRemoveItemSelectd(item?.orderId)}
          style={!item?.type ? { display: "none" } : {}}
          disabled={loading}
        >
          <span></span>
        </Button>
      </div>
    );
  });

  const onConfirmBetGame = () => {
    const leg = orderItem?.length || 0;
    const totalBuyMoney = leg * _value;

    if (!leg) return snackbar.show("  .", "danger");
    if (Number(totalBuyMoney) > Number(balance)) {
      return snackbar.show(
        " .    .",
        "danger"
      );
    }

    onBetGameOrder({
      typeGame: value,
      type: type,
    });
  };
  const getClassName = (type) => {
    switch (type) {
      case "LOTTO": {
        return "lotto-game";
      }
      default:
        return "powerball-game";
    }
  };

  return (
    <div className="result-select mt-3 ml-2">
      <Row>
        <Col span={24}>
          <div className=" content">
            <div className="title">
              <p className="bold m-0 f-16"> </p>
              <p className="small m-0 f-12">
                '    .
              </p>
            </div>

            <div className={`result-box ${getClassName(type)}`}>
              {renderItems}
            </div>

            <div className="button-buy flex-center">
              <div className="binance">
                <p className="m-0 pb-1"></p>
                <p className="m-0 value f-16 flex-center justify-start">
                  {formatMoney(balance)}
                </p>
              </div>

              <div className="binance">
                <p className="m-0 pb-1"></p>
                <p className="m-0 value f-16">
                  {formatMoney(orderItem.length * _value)} 
                </p>
              </div>
              <ButtonLottery
                text=""
                primary
                button={{
                  onClick: () => onConfirmBetGame(),
                  loading: loading,
                }}
              />
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};

BoxSelectNumber.propTypes = {
  orderItem: PropTypes.array,
  onRemoveItemSelectd: PropTypes.func,
  value: PropTypes.number,
  balance: PropTypes.number,
  onBetGameOrder: PropTypes.func,
  loading: PropTypes.bool,
  type: PropTypes.string,
};

export default connect((state) => {
  const { auth } = state;

  return {
    balance: auth?.balance || 0,
  };
})(BoxSelectNumber);
