import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Table, Pagination } from "antd";
import { connect } from "react-redux";

import { checkMobile } from "helper/helper";
import { BoxLottery, TitleBox } from "components";
import { COLUNMS_TRANSACTION } from "config/table";
import "./style.scss";

const MyPageFormTransaction = ({
  InquireTransaction,
  getMyInquireTransaction,
  count,
}) => {
  const [loading, SetLoading] = useState(true);
  const [pageIndex, SetPageIndex] = useState(1);
  const [pageSize, SetPageSize] = useState(20);

  useEffect(() => {
    SetPageSize(checkMobile() ? 10 : 20);
  }, []);
  useEffect(() => {
    mapData();
  }, [pageIndex, pageSize]);

  const mapData = async () => {
    SetLoading(true);

    await getMyInquireTransaction({ pageIndex, pageSize });
    SetLoading(false);
  };
  const renderItemMobile = InquireTransaction.map((_i, inde) => {
    if (_i?.typeTxt) {
      return (
        <div
          className="item-lottrey-mobi d-flex border-bottom-mobile"
          key={String(`__con-my-trac-${inde}`)}
        >
          <div className="content-lottrey">
            <div className="flex-center _item">
              <span className="text-2">{_i?.typeTxt || ""}</span>
              <span>{_i?.statusTxt || ""}</span>
            </div>
            <div className="flex-center _item">
              <span>{_i?.description || ""}</span>
            </div>
            <div className="flex-center _item">
              <span className="f-12">{_i?.timeConvert || ""}</span>
              <span className="text-2">{_i?.winMoneyText || ""}</span>
            </div>
          </div>
        </div>
      );
    }
  });
  return (
    <div className="pb-5 pt-3 my-transaction">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="content">
          <TitleBox name="  " fontSize={"1rem"} hiddenGach />
          <div className="table-content">
            <div className="table-desktop mt-5">
              <Table
                columns={COLUNMS_TRANSACTION}
                pagination={{ position: [] }}
                dataSource={InquireTransaction}
                loading={loading}
                pagination={false}
              />
            </div>
            <div className="table-mobile">
              <div className="border-bottom-mobile"></div>
              {renderItemMobile}
            </div>
          </div>
          <Pagination
            className={`pt-3 flex-center ${
              (count <= 0 && "__hidden-item") || ""
            }`}
            current={pageIndex}
            pageSize={pageSize}
            showSizeChanger={false}
            total={count}
            responsive={true}
            onChange={(val) => SetPageIndex(val)}
          />
        </div>
      </BoxLottery>
    </div>
  );
};

MyPageFormTransaction.propTypes = {
  InquireTransaction: PropTypes.array,
  getMyInquireTransaction: PropTypes.func,
  count: PropTypes.number,
};
export default connect(
  (state) => {
    const { history = {} } = state;

    return {
      InquireTransaction: history?.InquireTransaction || [],
      count: history?.count || 0,
    };
  },
  ({ history: { getMyInquireTransaction } }) => {
    return {
      getMyInquireTransaction,
    };
  }
)(MyPageFormTransaction);
