import React from 'react'
import './style.scss'
import PropTypes from 'prop-types';
import { STEP_ICON_REGISTER } from 'config/togger'


const StepIconBox = ({
    step = 0
}) => {
    return (
        <div className="step-box-icon mt-4 mb-4">
            <div className="group-step flex-center justify-between">
                {
                    STEP_ICON_REGISTER.map((item, inde) => {
                        return (
                            <div
                                key={String(inde)}
                                className="item-step"
                                style={{ opacity: inde <= step ? 1 : 0.5 }}
                            >
                                <img
                                    src={require(`resources/${item?.icon || ''}`)}
                                    alt="lottery"
                                    aria-roledescription="icon step"
                                />
                                <p className="f-16 mt-3">{item?.name || ''}</p>
                            </div>
                        )
                    })
                }
            </div>


        </div>
    )
}


StepIconBox.propTypes = {
    step: PropTypes.number
};
export default StepIconBox