import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types';
import { Form } from 'antd';
import { connect } from 'react-redux';


import {
    BoxLottery,
    TitleBox,
    ButtonLottery,
    InputLable,
} from "components";
import './style.scss'




const MyPageForm = ({
    infoUser,
    onChangePassword,
    banks
}) => {
    const [loading, SetLoading] = useState(false)
    const [userData, SetUserData] = useState({})


    useEffect(() => {
        const { bankid = 0, banknumber = '', bankholdername = '', phonenumber = '', id = 0 } = infoUser || {}


        if (id) {
            const bank = (banks || []).find(i => i.id == Number(bankid));
            const bankConvert = `${bank?.bankname} ${banknumber} ${bankholdername}`

            SetUserData({
                ...infoUser,
                phonenumber: String(phonenumber || '').replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3"),
                bankConvert: bankConvert
            })
        }
    }, [banks])



    
    const onFinish = async (value = {}) => {
        SetLoading(true);

        await onChangePassword({ ...value }).then().catch()
        SetLoading(false)
    }
    return (
        <div className="pb-5 pt-3 my-page" >
            <BoxLottery
                style={{ width: '100%', display: 'flex' }}
                span={24}
            >
                <div className="pt-3 pb-5 pl-2">
                    <TitleBox name=' ' fontSize={'1rem'} hiddenGach />
                </div>

                <div className="content">
                    <Form
                        name="basic"
                        layout="vertical"
                        initialValues={{
                            remember: true,
                        }}
                        onFinish={onFinish}
                    >

                        <Form.Item className="mb-075" >
                            <div className='d-flex align-end pb-2 flex-wrap w-full'>
                                <InputLable
                                    lable=""
                                    input={{
                                        placeholder: "",
                                        className: "hiden active",
                                        value: userData?.username || '',
                                        readOnly: true
                                    }}
                                />
                            </div>

                        </Form.Item>




                        {/* form change password */}
                        <div className="form-change-password mb-3">
                            <Form.Item
                                name="olPassword"
                                className="mb-075"
                                rules={[{
                                    required: true,
                                    message: ''
                                }]}
                            >
                                <div className='d-flex align-end pb-3 flex-wrap'>
                                    <InputLable
                                        lable=" "
                                        input={{
                                            placeholder: "",
                                            type: "password",
                                            maxLength: 34,
                                            readOnly: loading
                                        }}
                                    />
                                    <span className="mb-1 ml-3 txt">  </span>
                                </div>

                            </Form.Item>
                            <Form.Item
                                name="password"
                                className="mb-075"
                                rules={[{
                                    required: true,
                                    message: ''
                                }]}
                            >
                                <div className='d-flex align-end pb-3 flex-wrap'>
                                    <InputLable
                                        input={{
                                            placeholder: "",
                                            type: "password",
                                            maxLength: 34,
                                            readOnly: loading
                                        }}
                                    />
                                    <span className="mb-1 ml-3 txt">  </span>
                                </div>

                            </Form.Item>
                            <Form.Item
                                name="confirmPassword"
                                rules={[{
                                    required: true,
                                    message: ''
                                }]}
                            >
                                <div className='d-flex align-end flex-wrap'>
                                    <InputLable
                                        input={{
                                            placeholder: "",
                                            type: "password",
                                            maxLength: 34,
                                            readOnly: loading
                                        }}
                                    />
                                    <span className="mb-1 ml-3 txt">   </span>
                                </div>

                            </Form.Item>


                            {/* //button */}
                            <Form.Item className="mb-0 mt-1">
                                <div className="flex-center justify-between pb-3 bnt-content-policy">
                                    <ButtonLottery
                                        text=""
                                        button={{
                                            htmlType: "submit",
                                            loading: loading,
                                        }}
                                    />
                                </div>

                            </Form.Item>
                        </div>




                        <Form.Item className="mb-075" >
                            <div className='d-flex align-end pb-2 flex-wrap w-full'>
                                <InputLable
                                    lable=""
                                    input={{
                                        placeholder: "",
                                        className: "hiden active",
                                        value: userData?.bankConvert || '',
                                        readOnly: true
                                    }}
                                />
                            </div>
                        </Form.Item>
                        <Form.Item className="mb-075" >
                            <div className='d-flex align-end pb-2 flex-wrap w-full'>
                                <InputLable
                                    lable=""
                                    input={{
                                        placeholder: "",
                                        className: "hiden active",
                                        value: userData?.phonenumber || '',
                                        readOnly: true
                                    }}
                                />
                            </div>
                        </Form.Item>
                    </Form>


                </div>
            </BoxLottery>
        </div>
    )
}


MyPageForm.propTypes = {
    name: PropTypes.string,
    onNextStep: PropTypes.func
};

export default connect(
    state => {
        const { auth = {}, common = {} } = state;

        return {
            infoUser: auth?.auth || {},
            banks: common?.banksInfo?.rows || []
        }
    },
    ({ auth: { onChangePassword } }) => {
        return {
            onChangePassword
        };
    }
)(MyPageForm);