import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Spin } from "antd";
import { RedoOutlined } from "@ant-design/icons";

import { BoxLottery } from "components";
import { renderNameHistory } from "helper/game";
import "./style.scss";

const HistoryOrderGame = ({ history, getDataHistory, typeGame, type }) => {
  const [loading, SetLoading] = useState(false);

  useEffect(() => {
    onReloadHistory();
  }, []);

  const renderListHistory = history.map((_, index) => (
    <li key={index} className="item flex-center">
      <span className="user-name">{renderNameHistory(_?.username || "")}</span>
      <span className="result">
        {(_?.exacly || []).map((__i, ind) => {
          return (
            <span key={`______key-exacly-${ind}`}>
              <span className={__i?.isExacly ? "bold" : ""}>
                {__i?.number || ""}
              </span>
              {__i?.leg == ind ? "" : `, `}
            </span>
          );
        })}
        {/* {String(_?.fullbet || "").replaceAll(",", ", ")} */}
      </span>
      <span>
        {`${_?.timeConvert || ""}`}
        {/*  9.30.3:28 */}
      </span>
    </li>
  ));

  const onReloadHistory = async () => {
    SetLoading(true);

    await getDataHistory({
      typeGame: typeGame,
      type: type,
    })
      .then()
      .catch();
    SetLoading(false);
  };

  return (
    <div className="history-order-game">
      <BoxLottery style={{ width: "100%" }} span={24}>
        <div className="content-history">
          <div className="title flex-center mb-4">
            <span> </span>
            <RedoOutlined
              className="ic-refesh"
              onClick={() => onReloadHistory()}
            />
          </div>
          <ul id="scroll" className="content ">
            {renderListHistory}

            {!history.length && (
              <li className="item flex-center" style={{ height: 100 }}>
                <span className="user-name" style={{ width: "100%" }}>
                   
                </span>
              </li>
            )}
          </ul>
        </div>

        {loading && (
          <div className="loading flex-center spin">
            <Spin />
          </div>
        )}
      </BoxLottery>
    </div>
  );
};

HistoryOrderGame.propTypes = {
  history: PropTypes.array,
  getDataHistory: PropTypes.func,
  typeGame: PropTypes.number,
  type: PropTypes.string,
};
export default HistoryOrderGame;
