import React, { useState } from "react";
import PropTypes from "prop-types";
import { Col, Row } from "antd";

import BoxSelectNumber from "./components/BoxSelectNumber";
import BoxConfirmSelect from "./components/BoxConfirmSelect";
import ResultGameMobile from "./components/ResultGameMobile";
import ModalHistory from "./components/ModalHistory";
import { BoxLottery } from "components";
import { formatMoney } from "helper/helper";
import "./style.scss";

const BoxOrderPlayGame = ({
  orderItem,
  onRemoveItemSelectd,
  typeGame,
  onBetGameOrder,
  infoGame,
  loading,
  onRandomNumberMinMax,
  onSentResultOptions,
  type,
  result,
  getResult,
  isMobile,
  reLoadDataGame,
  endRoundOfGame,
  history,
  getDataHistory,
  roundofgame,
}) => {
  const renderImage = type !== "LOTTO" ? "Logo_game_pow" : "Logo_game_lotto";
  const textGame = type !== "LOTTO" ? " " : "";
  const [isModalVisible, setIsModalVisible] = useState(false);
  const _value =
    Number(typeGame?.type || "0") == 1000005
      ? 10000
      : Number(typeGame?.type || "0");

  return (
    <div className="mt-1">
      <BoxLottery style={{ width: "100%" }} span={24}>
        <div className="order-play">
          <div className="info-game flex-content-between align-end mt-3">
            <div className="info d-flex align-end">
              <img src={require(`resources/images/${renderImage}.png`)} />
              <div className="ml-2">
                <h5 className="m-0 color-000">
                  {`${textGame} ${formatMoney(_value || "0")} ${
                    roundofgame || 0
                  }`}
                </h5>
                <h3 className="m-0 color-000">
                  {`  ${formatMoney(
                    Number(infoGame?.totalmoney || 0)
                  )} `}
                </h3>
              </div>
            </div>
            <a href="/history/lottrey">
              <span className="f-16 color-000 _a"></span>
            </a>
          </div>

          <Row className="mt-5">
            <Col span={24} className="show-for-mobile">
              {/* show for mobile */}
              {isMobile && (
                <>
                  <ResultGameMobile
                    result={result}
                    getResult={getResult}
                    type={type}
                    endRoundOfGame={endRoundOfGame}
                    reLoadDataGame={reLoadDataGame}
                    typeGame={typeGame?.type || "0"}
                    roundofgame={roundofgame}
                  />
                  <ModalHistory
                    isModalVisible={isModalVisible}
                    typeGame={Number(typeGame?.type || "0")}
                    type={type}
                    history={history}
                    getDataHistory={getDataHistory}
                    setIsModalVisible={() => setIsModalVisible(false)}
                  />
                </>
              )}

              <div className="flex-center _i-retad mb-3">
                <span> </span>
                <span onClick={() => setIsModalVisible(true)}> </span>
              </div>
            </Col>
            <Col xxl={9} xl={9} lg={9} md={9} span={24}>
              <div className="mr-1">
                <BoxSelectNumber
                  onRandomNumberMinMax={onRandomNumberMinMax}
                  onSentResultOptions={onSentResultOptions}
                  type={type}
                />
              </div>
            </Col>
            <Col xxl={15} xl={15} lg={15} md={15} span={24}>
              <div className="ml-1">
                <BoxConfirmSelect
                  onRemoveItemSelectd={onRemoveItemSelectd}
                  orderItem={orderItem}
                  value={Number(typeGame?.type || "0")}
                  onBetGameOrder={onBetGameOrder}
                  loading={loading}
                  type={type}
                />
              </div>
            </Col>
          </Row>
        </div>
      </BoxLottery>
    </div>
  );
};

BoxOrderPlayGame.propTypes = {
  orderItem: PropTypes.array,
  onRemoveItemSelectd: PropTypes.func,
  typeGame: PropTypes.object,
  onBetGameOrder: PropTypes.func,
  infoGame: PropTypes.object,
  loading: PropTypes.bool,
  onRandomNumberMinMax: PropTypes.func,
  onSentResultOptions: PropTypes.func,
  type: PropTypes.string,
  result: PropTypes.object,
  getResult: PropTypes.func,
  isMobile: PropTypes.bool,
  reLoadDataGame: PropTypes.func,
  endRoundOfGame: PropTypes.string,
  history: PropTypes.array,
  getDataHistory: PropTypes.func,
  roundofgame: PropTypes.number,
};
export default BoxOrderPlayGame;
