import React, { useState, useEffect } from "react";
import { Collapse } from "antd";
import { CaretRightOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import { TitleBox } from "components";
import "./style.scss";
const { Panel } = Collapse;

const ListRequire = ({ getQuestionData, data }) => {
  // const [loading, SetLoading] = useState(false)

  useEffect(() => {
    getQuestionData();
  }, []);

  const renderHead = (item = {}, index) => {
    return (
      <div className="flex-center __header-panel">
        <span style={{ width: 25 }}>{data.length + 1 - index}</span>
        <span className="__hideMobile">{item?.textCategory || ""}</span>
        <span className="line-clamp-1" style={{ maxWidth: 350 }}>{item?.title || ""}</span>
        <span className="__hideMobile">{item?.timeConvert || ""}</span>
        <span className="__hideMobile">
          {item?.answercontent ? "" : " "}
        </span>
        <span className="bnthiddenshow" style={{ width: 60 }}>
          <span
            style={{ width: 60 }}
            className="flex-center"
            id={`_hiddenActive${index}`}
          >
            
            <CaretRightOutlined rotate={90} className="mr-1" />
          </span>
        </span>
      </div>
    );
  };
  const renderPanelContent = (item, index) => {
    return (
      <div className="submenu-panel">
        <p className="mt-3 mb-3 content-user">{item?.fullcontent || ""}</p>

        <div className="admin-awser">
          <div className="top flex-center mb-2 __showMobile">
            <span>{item?.textCategory || ""}</span>&nbsp;&nbsp;
            <span>{item?.timeConvert || ""}</span>
          </div>
          <div
            className="top flex-center mb-3"
            id={!item?.answercontent ? "d-none" : ""}
          >
            <div className="flex-center right">
              <img src={require("resources/images/logo_tab.png")} />
              <span className="ml-3"></span>
            </div>
            <div className="left">
              <span>{item?.timeConvertAnswer || ""}</span>
            </div>
          </div>
          <p className="pb-1">{item?.answercontent || ""}</p>
          <div className="bottom flex-center ">
            <span className="flex-center bold mr-3 __showMobile">
              {item?.answercontent ? "" : " "}
            </span>
            <span
              className="flex-center bold"
              onClick={() => onCloseItem(index)}
            >
              
              <CaretRightOutlined rotate={-90} className="mr-1" />
            </span>
          </div>
        </div>
      </div>
    );
  };

  const onCloseItem = (id = 0) => {
    document.getElementById(`bntHiddenPanel${id}`).click();
    document.getElementById(`_hiddenActive${id}`).style.display = "flex";
  };
  const onHiddenDropHead = (val = []) => {
    data.map((_, index) => {
      index = index + 1;
      const id = val.find((i) => i == index);

      if (id)
        document.getElementById(`_hiddenActive${index}`).style.display = "none";
      else
        document.getElementById(`_hiddenActive${index}`).style.display = "flex";
    });
  };
  return (
    <div className="list-deposit">
      <div className="mb-5">
        <TitleBox name="" />
      </div>

      <div className="content">
        <Collapse
          bordered={false}
          className="site-collapse-custom-collapse"
          onChange={(val) => onHiddenDropHead(val)}
        >
          {data.map((item, index) => {
            return (
              <Panel
                header={renderHead(item, index + 1)}
                key={String(index + 1)}
                forceRender={true}
                className="site-collapse-custom-panel mb-2"
                extra={<span hidden id={`bntHiddenPanel${index + 1}`} />}
              >
                {renderPanelContent(item, index + 1)}
              </Panel>
            );
          })}
        </Collapse>
      </div>
    </div>
  );
};

ListRequire.propTypes = {
  data: PropTypes.array,
  getQuestionData: PropTypes.func,
};
export default connect(
  (state) => {
    const { auth = {} } = state;

    return {
      data: auth?.question?.rows || [],
    };
  },
  ({ auth: { getQuestionData } }) => {
    return {
      getQuestionData,
    };
  }
)(ListRequire);
