import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Table, Pagination, Row, Col } from "antd";

import PowerBallConentMain from 'moduls/main/PowerBallConentMain'
import LottoConentMain from 'moduls/main/LottoConentMain'
import { BoxLottery, TitleBox } from "components";
import { checkMobile } from "helper/helper";
import "./style.scss";


const MyPageFormLotto = ({
  count,
  hiddenResult,
  onGetDataChange,
  columns,
  data,
  _pageSize,
}) => {
  const isMobile = checkMobile();
  const [loading, SetLoading] = useState(true);
  const [pageIndex, SetPageIndex] = useState(1);
  const [pageSize, SetPageSize] = useState(10);

  useEffect(() => {
    const _size = (_pageSize && _pageSize) || isMobile ? 10 : 20;

    SetPageSize(_size);
  }, []);
  useEffect(() => {
    mapData();
  }, [pageIndex, pageSize]);

  const mapData = async () => {
    SetLoading(true);
    await onGetDataChange({ pageIndex, pageSize });
    SetLoading(false);
  };

  const renderItemMobile = data.map((_i, inde) => {
    if (_i?.fullbet) {
      return (
        <div
          className="item-lottrey-mobi d-flex border-bottom-mobile"
          key={String(inde) + _i?.key}
        >
          <img
            className="mr-3"
            src={require(`resources/images/${
              _i?.gameid < 5 || _i?.gameid == 9 || _i?.gameid == 10
                ? "Logo_game_pow"
                : "Logo_game_lotto"
            }.png`)}
            height="50px"
          />
          <div className="flex-center content-lottrey">
            <ul className="m-0">
              <li className="text-2">
                {/* {_i?.fullbet || ""} */}
                {(_i?.exacly || []).map((_exc, _ind) => (
                  <span key={`____key-inqury-loto_${_ind}`}>
                    <span className={_exc?.isExacly ? "bold" : ""}>
                      {_exc?.number || 0}
                    </span>
                    {_ind == _exc?.leg ? "" : `, `}
                  </span>
                ))}
              </li>
              <li>{_i?.roundText || ""}</li>
              <li className="f-12 color-6D6D6D">{_i?.timeConvert || ""}</li>
            </ul>
            <ul className="m-0">
              <li className="text-right">{_i?.statusTxt || ""}</li>
              <li className="text-right color-6D6D6D ">
                {_i?.priceText || ""}
              </li>
              <li className="text-right text-2">{_i?.winMoneyText || ""}</li>
            </ul>
          </div>
        </div>
      );
    }
  });
  return (
    <div className="pb-5 pt-3 my-lottrey">
      {!hiddenResult && (
        <Row>
          <Col
            xxl={14}
            xl={14}
            lg={14}
            md={14}
            span={24}
            className={!isMobile ? "pr-2" : ""}
          >
            <PowerBallConentMain isMobile={isMobile} isNotMain />
          </Col>
          <Col xxl={10} xl={10} lg={10} md={10} span={24}>
            <LottoConentMain isMobile={isMobile} isNotMain />
          </Col>
        </Row>
      )}
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="content">
          <TitleBox name="  " fontSize={"1rem"} hiddenGach />

          <div className="table-content">
            <div className="table-desktop mt-5">
              <Table
                rowKey={(a) => String(`___key-history-lotory-key-${a?.key}`)}
                columns={columns}
                dataSource={data}
                loading={loading}
                pagination={false}
              />
            </div>
            <div className="table-mobile">
              <div className="border-bottom-mobile"></div>
              {renderItemMobile}
            </div>
          </div>
        </div>
        <Pagination
          className={`pt-3 pb-3 flex-center ${
            (count <= 0 && "__hidden-item") || ""
          }`}
          current={pageIndex}
          pageSize={pageSize}
          showSizeChanger={false}
          total={count}
          responsive={true}
          onChange={(val) => SetPageIndex(val)}
        />
      </BoxLottery>
    </div>
  );
};

MyPageFormLotto.propTypes = {
  data: PropTypes.array,
  count: PropTypes.number,
  onGetDataChange: PropTypes.func,
  whitelistedTypeGames: PropTypes.instanceOf(Set),
  whitelistedTypes: PropTypes.instanceOf(Set),
  columns: PropTypes.array,
  _pageSize: PropTypes.number,
};

export default MyPageFormLotto;
