import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types';
import { CaretRightOutlined } from '@ant-design/icons';
import { Collapse } from 'antd';

import { TitleBox, } from "components";
import './style.scss'
import { connect } from 'react-redux';
import { isArray } from 'lodash';
const { Panel } = Collapse;

const AdminMessage = ({
    data,
    getDataAdminMessage,
    onSetStatusAdminMess
}) => {
    const [messData, setMess] = useState([])

    useEffect(() => {
        getDataAdminMessage()
    }, [])
    useEffect(() => {
        if (isArray(data)) setMess(data)
    }, [data])



    const renderHead = (item = {}, index) => {
        return <div className="flex-center __header-panel" onClick={() => onReadMessage(item)}>
            <span className="mr-1">{data.length - (index - 1)}</span>
            <span className="line-clamp-1 mr-1">{item?.title || ''}</span>
            <span className="__hideMobile">{item?.timeConvert || ''}</span>
            <span style={{ width: 60 }}>{item?.status == 'N' ? '' : ''}</span>
            <span className="bnthiddenshow">
                <span style={{ width: 57 }} className="flex-center" id={`_hiddenActive${index}`}>
                    
                    <CaretRightOutlined rotate={90} className="mr-1" />
                </span>
            </span>
        </div>
    }
    const renderPanelContent = (item, index) => {
        return <div className="submenu-panel">
            <p className="mt-3 mb-3 content-user">
                {item?.content || ''}
            </p>

            <div className="admin-awser">
                {/* <div className="top flex-center mb-3">
                    <div className="flex-center right">
                        <img src={require('resources/images/logo_tab.png')} />
                        <span className="ml-3"></span>
                    </div>
                    <div className="left">
                        <span>2021.8.11.12:39</span>
                    </div>
                </div>
                <p className="pb-1">      </p> */}
                <div className="bottom">
                    <span className="flex-center" onClick={() => onCloseItem(index, item)}>
                        
                        <CaretRightOutlined rotate={-90} className="mr-1" />
                    </span>
                </div>
            </div>
        </div>
    }

    const onCloseItem = (id = 0, item = {}) => {
        document.getElementById(`bntHiddenPanel${id}`).click()
        document.getElementById(`_hiddenActive${id}`).style.display = "flex"


    }
    const onHiddenDropHead = (val = []) => {

        data.map((_, index) => {
            index = index + 1
            const id = val.find(i => i == index)

            if (id) document.getElementById(`_hiddenActive${index}`).style.display = "none"
            else document.getElementById(`_hiddenActive${index}`).style.display = "flex"
        })
    }
    const onReadMessage = (item) => {
        const arr = messData.map(i => {
            if (i.id == item.id) {
                onSetStatusAdminMess({ id: i.id })
                return { ...i, status: 'D' }
            }
            return { ...i }
        })

        setMess([...arr])
    }
    return (
        <div className="pb-5 pt-3 m-admin-messager" >
            <div className="content">
                <div className="list-admin-messager">
                    <div className="mb-5">
                        <TitleBox name='' />
                    </div>

                    <div className="content">
                        <Collapse
                            bordered={false}
                            className="site-collapse-custom-collapse"
                            onChange={(val) => onHiddenDropHead(val)}
                        >
                            {
                                messData.map((item, index) => {
                                    return <Panel
                                        header={renderHead(item, index + 1)}
                                        key={String(index + 1)}
                                        forceRender={true}
                                        className="site-collapse-custom-panel mb-2"
                                        extra={<span hidden id={`bntHiddenPanel${index + 1}`} />}
                                    >
                                        {renderPanelContent(item, index + 1)}
                                    </Panel>
                                })
                            }
                        </Collapse>
                    </div>
                </div>
            </div>
        </div>
    )
}


AdminMessage.propTypes = {
    data: PropTypes.array,
    getDataAdminMessage: PropTypes.func,
    onSetStatusAdminMess: PropTypes.func,

};
export default connect(
    state => {
        const { auth = {} } = state
        return {
            data: auth?.admin?.rows || []
        }
    },
    ({ auth: {
        getDataAdminMessage,
        onSetStatusAdminMess
    } }) => {
        return {
            getDataAdminMessage,
            onSetStatusAdminMess
        }
    }
)(AdminMessage)