import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Spin } from "antd";

import { Countdown, TitleBox } from "components";
import { getColorFollowValue } from "helper/game";
import { convertTimeUTCtoLocal } from "helper/helper";
import "./style.scss";
import { isArray, isNumber } from "lodash";
import moment from "moment";

const ResultGameMobile = ({
  result,
  getResult,
  type,
  reLoadDataGame,
  endRoundOfGame,
  typeGame,
  roundofgame,
}) => {
  const [loading, SetLoading] = useState(false);

  useEffect(() => {
    getDataResult();
  }, []);

  const getDataResult = async () => {
    SetLoading(true);

    await getResult({
      typeGame: typeGame,
      type: type,
    });
    SetLoading(false);
  };

  const onReloadData = () => {
    reLoadDataGame({
      typeGame: typeGame,
      type: type,
    });
  };

  const renderListResult =
    isArray(result.data) &&
    result.data.map((item, index) => {
      if (isNumber(item)) {
        if (
          (index == 6 && type == "LOTTO") ||
          (index == 5 && type !== "LOTTO")
        ) {
          return (
            <div key={String(index)} className="flex-center">
              <span className="ic-plus"></span>
              <div
                className="item flex-center"
                style={{ borderColor: getColorFollowValue(item) }}
              >
                <span>{item || 0}</span>
              </div>
            </div>
          );
        }

        return (
          <div key={String(index)}>
            {index <= (type == "LOTTO" ? 5 : 4) && (
              <div
                className="item flex-center mr-2"
                style={{ borderColor: getColorFollowValue(item) }}
              >
                <span>{item || 0}</span>
              </div>
            )}
          </div>
        );
      }
    });

  const renderTextDate = (date = new Date()) => {
    const value = moment(convertTimeUTCtoLocal(date));

    const year = value.format("YYYY");
    const month = value.format("MM");
    const day = value.format("DD");
    const hour = value.format("HH:mm");

    return `${year} ${month} ${day} ${hour} `;
  };

  return (
    <div id="result-game-for-mobile">
      <div className="content-result-for-mobile">
        <div className="title">
          <TitleBox
            name={`${
              type == "LOTTO" ? result?.drawno || 0 : result?.roundofdate || 0
            } `}
            hiddenGach
            fontSize={20}
          />
          <span className="font-small" style={{ marginTop: -10 }}>
            {renderTextDate(result?.createdat || new Date())}
          </span>
        </div>
        <div className="result flex-center mt-4 mb-4">{renderListResult}</div>
        <div className="count-down flex-center">
          <span className="mr-1">{`${roundofgame}   `}</span>
          {(endRoundOfGame && (
            <Countdown
              dateTime={endRoundOfGame}
              callback={onReloadData}
              type={type}
            />
          )) ||
            null}
        </div>
      </div>
      <div
        style={{ display: (loading && "flex") || "none" }}
        className="loading flex-center spin"
      >
        <Spin />
      </div>
    </div>
  );
};

ResultGameMobile.propTypes = {
  result: PropTypes.object,
  getResult: PropTypes.func,
  reLoadDataGame: PropTypes.func,
  endRoundOfGame: PropTypes.string,
  typeGame: PropTypes.string,
  roundofgame: PropTypes.number,
};
export default ResultGameMobile;
