import React from "react";
import PropTypes from "prop-types";
import { Row, Col } from "antd";

import Policy from "./components/Policy";
import TableRulesWinning from "./components/Table";
import { BoxLottery, TitleBox } from "components";
import "./style.scss";

const data = [
  {
    typeGame: "",
    priceText: "5  ,   ",
    roundText: "982,800.00",
  },
  {
    typeGame: "",
    priceText: "5  ,   ",
    roundText: "982,800.00",
  },
  {
    typeGame: "",
    priceText: "5  ,   ",
    roundText: "982,800.00",
  },
];

const COLUNMS_LOTTO = [
  {
    title: "",
    dataIndex: "typeGame",
    key: "typeGame",
    align: "center",
    width: 100,
  },
  {
    title: "",
    dataIndex: "priceText",
    key: "priceText",
    align: "center",
  },
  {
    title: "(1/)",
    dataIndex: "roundText",
    key: "roundText",
    align: "center",
    width: 200,
  },
];
const CheckingSystem = ({ title }) => {
  return (
    <div className="pb-5 pt-3 m-rules-winning">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="content ">
          <Row>
            <Col span={24}>
              <TitleBox name={title} fontSize={"1.25rem"} hiddenGach />
              <div className="content-rules-winning pl-2 mt-2">
                <Policy />
                <div className="mt-3 table">
                  <TableRulesWinning
                    data={data}
                    columns={COLUNMS_LOTTO}
                    label=" "
                  />
                </div>
                <div className="mt-3 table">
                  <TableRulesWinning
                    data={data}
                    columns={COLUNMS_LOTTO}
                    label=" "
                  />
                </div>
              </div>
            </Col>
          </Row>
        </div>
      </BoxLottery>
    </div>
  );
};

CheckingSystem.propTypes = {
  type: PropTypes.string,
};
export default CheckingSystem;
