import React, { useEffect, useState } from "react";
import { Modal } from "antd";

import HistoryOrderGame from "../../../BoxInfoAndResultOrder/component/HistoryOrderGame";
import "./style.scss";

const ModalHistory = ({
  isModalVisible,
  setIsModalVisible,
  type,
  typeGame,
  getDataHistory,
  history,
}) => {
  return (
    <Modal visible={isModalVisible}>
      <div id="modal-game">
        <HistoryOrderGame
          typeGame={typeGame}
          type={type}
          history={history}
          getDataHistory={getDataHistory}
        />
        <div
          className="bnt-close-modal  flex-center pt-2 pb-2"
          onClick={setIsModalVisible}
        >
          <span className="close" />
          <span className="bold color-000 f-16" style={{ color: '#000000A6'}}></span>
        </div>
      </div>
    </Modal>
  );
};

export default ModalHistory;
