import React from 'react'
import PropTypes from 'prop-types';

import { DEPOSIT_PO, WITHDRAW_PO } from 'config/policy'
import './style.scss'


const PolicyDeposit = ({
    type
}) => {

    const data = type == 'WITHDRAW' ? WITHDRAW_PO : DEPOSIT_PO
    return (
        <div className="deposit-content" dangerouslySetInnerHTML={{ __html: data }} />
    )
}

PolicyDeposit.propTypes = {
    type: PropTypes.string,
};
export default PolicyDeposit