import React from "react";
import PropTypes from "prop-types";
import { Table } from "antd";
import { TitleBox } from "components";
import "./style.scss";

const TableRulesWinning = ({ columns, data, loading, label }) => {
  return (
    <div className="table-rules-winning">
      <TitleBox name={label} fontSize={"1rem"} hiddenGach />
      <Table
        columns={columns}
        pagination={{ position: [] }}
        dataSource={data}
        loading={loading}
      />
      <div className="flex-content-between bottom-table">
        <span className="bold total-money-win"> </span>
        <span>   0.8   </span>
      </div>
    </div>
  );
};

TableRulesWinning.propTypes = {
  columns: PropTypes.array,
  data: PropTypes.array,
  loading: PropTypes.bool,
  label: PropTypes.string,
};

export default TableRulesWinning;
