import React from 'react'
import PropTypes from 'prop-types';
import { Col, Row } from 'antd'

import './style.scss'
import ResultGame from './component/ResultGame'
import HistoryOrderGame from './component/HistoryOrderGame'


const BoxInfoAndResultOrder = ({
    result,
    history,
    getResult,
    getDataHistory,
    type,
    typeGame,
    reLoadDataGame,
    endRoundOfGame,
    roundofgame
}) => {
    return (
        <div id="box-info-result-order">
            <Row>
                <Col span={13}>
                    <div className="mr-1">
                        <ResultGame
                            result={result}
                            getResult={getResult}
                            type={type}
                            reLoadDataGame={reLoadDataGame}
                            typeGame={typeGame}
                            endRoundOfGame={endRoundOfGame}
                            roundofgame={roundofgame}
                        />
                    </div>
                </Col>
                <Col span={11}>
                    <div className="ml-1">
                        <HistoryOrderGame
                            history={history}
                            getDataHistory={getDataHistory}
                            typeGame={typeGame}
                            type={type}
                        />
                    </div>
                </Col>
            </Row>
        </div>


    )
}


BoxInfoAndResultOrder.propTypes = {
    result: PropTypes.object,
    getResult: PropTypes.func,
    type: PropTypes.string,
    typeGame: PropTypes.number,
    roundofgame: PropTypes.number,
    endRoundOfGame: PropTypes.string,

    history: PropTypes.array,
    getDataHistory: PropTypes.func,
    reLoadDataGame: PropTypes.func,
};
export default BoxInfoAndResultOrder